html, body {
    padding: 0%;
    margin: 0%;
    text-decoration: none;
    overflow-x: hidden;
}

.item {
    color: white;
}

.sidebar {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
    padding: 5vh 0 0;
    box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}

.main {
    background-image: url("./what_do_you_see__by_theghostvirus-d3j7ig3.jpg"), radial-gradient(rgb(146, 56, 56), rgb(114, 114, 114));
    height: initial;
    background-blend-mode: lighten;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right;
    background-attachment: fixed;
}

.main-stats {
    background-image: url("./what_do_you_see__by_theghostvirus-d3j7ig3.jpg"), radial-gradient(rgb(146, 56, 56), rgb(114, 114, 114));
    height: initial;
    background-blend-mode: lighten;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 10vh;
}

.sidebar-sticky {
    position: relative;
    top: 5vh;
    height: calc(100vh - 48px);
    padding-top: .5rem;
    overflow-x: hidden;
    overflow-y: auto;
    font-size: 3vh;
}

.sidebar .nav-link {
    font-weight: 500;
    color: #333;
}

.navbar-brand {
    padding-top: .45rem;
    padding-bottom: .45rem;
    font-size: 1.5rem;
    box-shadow: inset -1px 0 0 rgba(0, 0, 0, .25);
    text-decoration: none;
}

.logout {
    font-size: 1rem;
}

.elems a {
    transition: linear 0.3s;
}

.elems:hover a {
    background-color: rgb(51, 51, 51);
    color: white;
}

.atr {
    width: 65vw;
    margin-left: 10vw;
    background-color: rgba(58, 38, 38, 0.8);
    color: white;
}

.top-stat {
    position: relative;
    top: 20vh;
    left: 13vw;
    width: 80vw;
}

.card-stat {
    background-color: rgba(58, 38, 38, 0.8);
    width: 20vw;
    color: white;
    padding: 2vw;
    margin: 2vw;
    margin-bottom: 15vh;
    display: inline-block;
    border-radius: 5px;
}

.stat-quest {
    color: white;
    background-color: rgba(58, 38, 38, 0.8);
    width: 40vw;
    border-radius: 5px;
}

.stat-title {
    font-size: 2vw;
}

.stat {
    position: relative;
    left: 15vw;
    width: 65vw;
    background-color: white;
    border-radius: 5px;
    padding-bottom: 5vh;
}

.quest-title {
    width: 60vw;
    padding: 5vh;
    text-align: center;
}

.var-title {
    width: 20vw;
    padding: 5vh;
    text-align: center;
}

.stat-table {
    width: 60vw;
    text-align: center;
    margin: 3vw;
}