.questionnaires {
    width: 100vw;
}

.quest-background {
    position: fixed;
    top: 0vh;
    left: 0vw;
    width: 100vw;
    height: 100vh;
    background-image: url("./what_do_you_see__by_theghostvirus-d3j7ig3.jpg"), radial-gradient(rgb(146, 56, 56), rgb(114, 114, 114));
    background-blend-mode: lighten;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right;
    background-attachment: fixed;
}

.search {
    position: fixed;
    top: 80vh;
    left: 15vw;
    transform: translate(-50%, -50%);
    border-radius: 40px;
    background-image: linear-gradient(45deg, rgb(189, 48, 48), rgb(34, 50, 201));
    padding-left: 1vw;
    padding-right: 1vw;
    padding-top: 3vh;
    transition: linear 0.3s;
    z-index: 1;
    opacity: 0.9;
}

.search-input {
    width: 0;
    height: 0;
    visibility: hidden;
    transition: linear 0.3s;
    background-color: rgba(41, 41, 41, 0.39);
    border: none;
    color: white;
}

.search-btn {
    background-color: transparent;
    border: none;
    color: white;
}

.search-btn img {
    width: 3vw;
}

.search:hover {
    padding-right: 0;
    padding-left: 2vw;
}

.search:hover .search-input {
    width: 20vw;
    height: 5vh;
    visibility: visible;
}

.questionnaire {
    color: white;
    background-color: rgba(56, 56, 56, 0.795);
    border-radius: 5px;
    transition: ease-in-out 0.5s;
    width: 45vw;
}

.questionnaire:hover {
    box-shadow: -10px -10px 20px rgba(150, 32, 32, 0.6), 10px 10px 20px  rgba(25, 25, 117, 0.6);
}

.card-title {
    margin-bottom: 5vh;
}

.list-qq, .list-q {
    list-style: none;
}

.list-q {
    margin-top: 15vh;
    margin-left: 12vw;
    width: 100vw;
}

.q {
    position: relative;
    left: 13vw;
    width: 40vw;
    margin-bottom: 10vh;
    margin-top: 5vh;
}

.qq {
    text-align: center;
    margin-left: -3vw;
    margin-top: 2vh;
    margin-bottom: 2vh;
}

.card-btn a {
    text-decoration: none;
    color: white;
}

.quests {
    margin-left: 15vw;
}

.answer {
    transition: linear 0.3s;
    z-index: 2;
}

.answer-form {
    color: white;
    height: 0;
    overflow: hidden;
    visibility: hidden;
}

.answer-title {
    color: white;
    animation: glow 1s ease-in-out infinite alternate;
}

.answer:hover {
    position: fixed;
    top: 50vh;
    left: 50vw;
    transform: translate(-50%, -50%);
    width: 100vw;
    height: 100vh;
    background-color: rgba(54, 54, 54, 0.822);
    visibility: visible;
}

.answer:hover .answer-title {
    visibility: hidden;
}

.answer:hover .answer-form {
    position: fixed;
    top: 50vh;
    left: 50vw;
    transform: translate(-50%, -50%);
    height: initial;
    visibility: visible;
}

.answerd {
    text-decoration: line-through;
}

@keyframes glow {
    from {
        text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #111e96, 0 0 40px #111e96, 0 0 50px #111e96, 0 0 60px #111e96, 0 0 70px #111e96;
      }
      to {
        text-shadow: 0 0 20px #fff, 0 0 30px #b41717, 0 0 40px #b41717, 0 0 50px #b41717, 0 0 60px #b41717, 0 0 70px #b41717, 0 0 80px #b41717;
      }
}

.identify {
    position: fixed;
    top: 57vh;
    left: 50vw;
    transform: translate(-50%, -50%);
    width: 100vw;
    height: 90vh;
    padding: 12%;
    border-radius: 5px;
    color: white;
    background-color: rgba(54, 54, 54, 0.822);
    z-index: 2000;
}

.remove {
    visibility: hidden;
    width: 0;
    height: 0;
}

.form-ident {
    position: fixed;
    top: 50vh;
    left: 50vw;
    transform: translate(-50%, -50%);
}