html, body {
    margin: 0;
    padding: 0;
}

.home {
    background-image: linear-gradient(237.94deg, rgba(175, 56, 56, 0.61) 0%, rgba(64, 90, 158, 0.58) 100%), url('./no-description-1301831 1.jpg');
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0vh;
    left: 0vw;
    background-size: cover;
    background-position: center;
    opacity: 0.8;
}

.body1 {
    position: absolute;
    top: 35vh;
    width: 50vw;
    left: 30vw;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 3rem;
    text-align: center;
}

